.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  width: 100%;
  height: 56px;
  border: 2px solid $colorBlue;
  border-radius: 8px;

  font-weight: bold;
  font-size: 16px;
  line-height: 14px;
  text-decoration: none;
  color: $colorBlue;
  cursor: pointer;

  transition: all 0.3s linear;

  &--primary {
    background-color: #005ecc;
    color: #fff;
    border-color: #005ecc;
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  &:hover {
    color: #fff;
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
  }

  &:active {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 8px 14px rgba(0, 83, 181, 0.46);
  }
}
